<template>
  <div class="ApplyUpgrade">
    <TitleBtn title="申请升级"></TitleBtn>
    <div class="ApplyUpgrade_topbtn" @click="jumpLink('merchantinfo')">商家信息</div>
    <div class="ApplyUpgrade_content">
      <div>您当前的等级：普通会员</div>
      <div>您可申请升级的等级：一星会员</div>
      <div>是否提交申请？</div>
    </div>
    <div class="ApplyUpgrade_btn">
      <el-button style="width: 100%;" type="primary" plain>提交申请</el-button>
    </div>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
export default {
  components: {
    TitleBtn
  },
  data () {
    return {
    }
  },
  methods: {
    jumpLink: function(link) {
      this.$router.push({path: '/' + link})
    }
  }
}
</script>

<style scoped>
  .ApplyUpgrade {
    position: relative;
  }

  .ApplyUpgrade_topbtn {
    color: #428bca;
    position: absolute;
    right: .2rem;
    top: .25rem;
  }

  .ApplyUpgrade_content {
    width: 80%;
    margin: 1rem auto .8rem;
    background-color: #f2f2f2;
    padding: .2rem;
    line-height: 1.5;
  }

  .ApplyUpgrade_btn {
    width: 45%;
    margin: 0 auto;
  }
</style>



