<template>
  <div id="register">
    <TitleBtn title="帮助注册页面"></TitleBtn>
    <div class="register_content">
      <el-form label-position="top" :rules="rules" ref="form" :model="form" label-width="80px">
        <el-form-item label="手机号码:" prop="mobile">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
        <el-form-item label="微信号码:" prop="weixin">
          <el-input v-model="form.weixin"></el-input>
        </el-form-item>
        <el-form-item label="姓名:" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="password">
          <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item class="">
          <el-button class="register_content_submit" type="primary" @click="submitForm('form')">立即注册</el-button>
          <el-button class="register_content_reset" @click="resetForm('form')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
export default {
  components: {
    TitleBtn
  },
  data () {
    return {
      form: {
        mobile: '',
        weixin: '',
        username: '',
        password: ''
      },
      rules: {
        mobile: [
          { required: true, message: '请输入手机号码', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入正确的手机号码', trigger: 'blur' }
        ],
        weixin: [
          { required: true, message: '请输入微信号码', trigger: 'blur' },
          { min: 6, max: 20, message: '请输入正确的微信号码', trigger: 'blur' }
        ],
        username: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 2, max: 20, message: '请输入正确的姓名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 4, max: 20, message: '请输入正确的密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        }
      })
    }
  }
}
</script>

<style scoped>
  .register_content {
    width: 90%;
    margin: .3rem auto 0;
  }

  #register >>> .el-form-item__error {
    top: -90%;
    right: 0;
    left: auto;
  }

  .register_content_submit {
    width: 30%;
    margin-top: .2rem;
  }

  .register_content_reset {
    width: 30%;
    margin-left: .5rem;
    margin-top: .2rem;
  }

  #register >>> .el-form-item__content:last-child {
    display: flex;
    justify-content: center;
  }

  #register >>> .el-form-item__label {
    line-height: .4rem;
  }
</style>



