<template>
  <div class="ChangePassword">
    <TitleBtn title="修改密码"></TitleBtn>
    <div class="ChangePassword_content">
      <el-form label-position="top" :rules="rules" ref="form" :model="form" label-width="80px">
        <el-form-item label="您的旧密码:" prop="oldpassword">
          <el-input v-model="form.oldpassword"></el-input>
        </el-form-item>
        <el-form-item label="您的新密码:" prop="newpassword">
          <el-input v-model="form.newpassword"></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="affirmpassword">
          <el-input v-model="form.affirmpassword"></el-input>
        </el-form-item>
        <el-form-item label="您的手机号码:" prop="mobile">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
        <el-form-item label="手机验证码:" prop="authcode">
          <el-input v-model="form.authcode">
            <el-button type="danger">发送验证码</el-button>
          </el-input>
        </el-form-item>
        <el-form-item class="">
          <el-button class="ChangePassword_content_submit" type="danger" @click="submitForm('form')">确认修改</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
export default {
  components: {
    TitleBtn
  },
  data () {
    return {
      form: {
        oldpassword: '',
        newpassword: '',
        affirmpassword: '',
        mobile: '',
        weixin: '',
        username: '',
        password: '',
        authcode: ''
      },
      rules: {
        mobile: [
          { required: true, message: '请输入手机号码', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入正确的手机号码', trigger: 'blur' }
        ],
        oldpassword: [
          { required: true, message: '请输入旧的密码', trigger: 'blur' },
          { min: 4, max: 20, message: '请输入正确的密码', trigger: 'blur' }
        ],
        newpassword: [
          { required: true, message: '请输入新的密码', trigger: 'blur' },
          { min: 4, max: 20, message: '请输入正确的密码', trigger: 'blur' }
        ],
        affirmpassword: [
          { required: true, message: '请输入新的密码', trigger: 'blur' },
          { min: 4, max: 20, message: '请输入正确的密码', trigger: 'blur' }
        ],
        authcode: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 4, max: 6, message: '请输入正确的验证码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        }
      })
    }
  }
}
</script>

<style scoped>
  .ChangePassword_content {
    width: 90%;
    margin: .2rem auto 0;
  }

  .ChangePassword >>> .el-form-item__error {
    top: -70%;
    right: 0;
    left: auto;
  }

  .ChangePassword >>> .el-form--label-top .el-form-item__label {
    padding: 0;
  }

  .el-form-item {
    margin: 0;
  }

  .ChangePassword_content_submit {
    margin-top: .4rem;
    width: 100%;
  }
</style>



