<template>
  <div class="MyTeam">
    <TitleBtn title="我的团队(0人)"></TitleBtn>
    <div class="MyTeam_top">
      <div class="MyTeam_top_left">成员信息</div>
      <div class="MyTeam_top_right">一星及一星以上人数:0</div>
    </div>
    <div class="MyTeam_nodata" v-if="is_nodata">
      <div class="MyTeam_nodata_icon">
        <img src="../assets/nodata.png" alt="">
      </div>
      <div class="MyTeam_nodata_text">您还没有相关纪录~</div>
    </div>
    <div class="MyTeam_content" v-if="!is_nodata">
      <div class="MyTeam_content_list">
        <div class="MyTeam_content_list_top">
          <div>昵称:武宏亮律师</div>
          <div>等级:一星会员</div>
        </div>
        <div class="MyTeam_content_list_bottom">
          <div>手机号: 13925076527</div>
          <div>微信: 13925076527</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
export default {
  components: {
    TitleBtn
  },
  data () {
    return {
      is_nodata: false
    }
  },
  methods: {
  }
}
</script>

<style scoped>
  .MyTeam_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .25rem .2rem;
    border-bottom: 1px solid #f4f4f5;
  }

  .MyTeam_top_left {
    color: #666;
  }

  .MyTeam_top_right {
    color: #989898;
  }

  .MyTeam_nodata {
    margin: 2.5rem 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .MyTeam_nodata_icon {
    width: 1.5rem;
    margin-bottom: .2rem;
  }

  .MyTeam_content_list {
    padding: .3rem .2rem;
    border-bottom: 1px solid #eaeaea;
  }

  .MyTeam_content_list_top {
    color: #7a6666;
    margin-bottom: .2rem;
  }

  .MyTeam_content_list_bottom {
    color: #989898;
    font-size: .24rem;
  }

  .MyTeam_content_list_top, .MyTeam_content_list_bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
</style>



