import axios from 'axios'
import qs from 'qs'

// 基础路径
axios.defaults.baseURL = 'http://maker.begeel.com'
// 过期时间
axios.defaults.timeout = 5000
// 设置头部
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'

export const login = (data) => {
  return axios.post('/public/api/user/login', qs.stringify(data)).then(res => res.data)
}

export const getInfo = () => {
  return axios.post('/public/api/user/info', {}).then(res => res.data)
}


