<template>
  <div class="Settings">
    <TitleBtn title="设置"></TitleBtn>
    <div class="Settings_content">
      <div class="Settings_content_btn" @click="jumpLink('exitdata')">
        <div class="Settings_content_btn_text">修改个人资料: 97972</div>
        <i class="Settings_content_btn_icon el-icon-arrow-right"></i>
      </div>
      <div class="Settings_content_btn" @click="jumpLink('changepw')">
        <div class="Settings_content_btn_text" style="color: #504f4d;">修改密码</div>
        <i class="Settings_content_btn_icon el-icon-arrow-right"></i>
      </div>
    </div>
    <div class="Settings_btn">
      <el-button style="width: 100%;" type="danger" @click="dialogVisible = true">退出账户</el-button>
    </div>
    <el-dialog
    title="提示"
    width="80%"
    :visible.sync="dialogVisible"
    :before-close="handleClose"
    style="margin-top: 15vh;">
      <i class="el-icon-warning" style="color: #f57272;font-size: .5rem;"></i>
      <span>是否退出登录?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="logout">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import TitleBtn from '@/components/TitleBtn'
export default {
  components: {
    TitleBtn
  },
  data () {
    return {
      dialogVisible: false
    }
  },
  methods: {
    logout() {

    },
    handleClose() {
      this.dialogVisible = !this.dialogVisible
    },
    jumpLink(link) {
      this.$router.push({path: '/' + link})
    }
  }
}
</script>

<style scoped>
  .Settings_content {
    margin-top: .2rem;
    background-color: #fff;
  }

  .Settings_content_btn {
    border-top: 1px solid #f4f4f4;
    color: #9d9c9b;
    position: relative;
    padding: .2rem;
    line-height: 2;
  }

  .Settings_content_btn:last-child {
    border-bottom: 1px solid #f4f4f4;
  }

  .Settings_content_btn:active {
    background-color: #ececec;
  }

  .Settings_content_btn_icon {
    position: absolute;
    right: .2rem;
    top: 50%;
    transform: translateY(-50%);
    font-size: .25rem;
    font-weight: 900;
  }

  .Settings_btn {
    width: 90%;
    margin: .2rem auto 0;
  }
  
  #Settings >>> .el-message-box {
    width: 1.2px !important;
  }
</style>



