<template>
  <div class="login">
    <div class="login_title">新零售会员登录</div>
    <div class="login_content">
      <div class="login_content_input">
        <input type="number" v-model="mobile" name="mobile" placeholder="请输入您的手机号码">
        <div class="login_content_input_icon">
          <img src="../assets/uname.png" alt="">
        </div>
      </div>
      <div class="login_content_input">
        <input type="text" name="password" placeholder="请输入您的密码" v-model="password">
        <div class="login_content_input_icon">
          <img src="../assets/upsw.png" alt="">
        </div>
      </div>
      <div class="login_content_btn">
        <el-button style="width: 100%;" type="primary" @click="submit">登录</el-button>
      </div>
      <div class="login_content_text">
        <div style="width: auto;cursor: pointer;color: #409eff;">
          忘记密码?
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { login } from '../api/index.js'
export default {
  data () {
    return {
      mobile: '',
      password: ''
    }
  },
  methods: {
    submit() {
      if (this.mobile != '' && this.password != '') {
        login({
          mobile: this.mobile,
          password: this.password
        }).then(res => {
          if (res.code == 0) {
            this.$router.push({path: '/'})
          }
        })
      }
    }
  }
}
</script>

<style scoped>
  .login_title {
    line-height: 45px;
    border-bottom: 1px solid #efefef;
    text-align: center;
  }

  .login_content {
    padding: 0 .2rem;
  }

  .login_content_input {
    width: 100%;
    height: .9rem;
    position: relative;
    padding-left: .8rem;
    box-sizing: border-box;
    border: 1px solid #e8e8e8;
    margin-top: .4rem;
  }

  .login_content_input input {
    width: 100%;
    height: 95%;
    border: none;
    font-size: .32rem;
    color: #000;
    padding-left: .05rem;
  }

  .login_content_input input::placeholder {
    color: #757575;
  }

  .login_content_input_icon {
    width: .6rem;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 .1rem;
    border-right: 1px solid #e8e8e8;
  }

  .login_content_btn {
    margin-top: .4rem;
  }

  .login_content_text {
    display: flex;
    justify-content: flex-end;
    margin-top: .2rem;
  }
</style>



